.o-searchBar{
    overflow: hidden;
    border-radius: var(--spacing-80);
    svg{
        border-radius: var(--spacing-80);
        background-color: $prime_purple;
        aspect-ratio: 1/1;
        transform: translateX(var(--spacing-32));
        z-index: 2;
        g{
            transform-origin: 50% 50%;
            transform: scale(0.8);
        }
    }
    &__input{
        width: 100%;
        height: 100%;
        border: solid var(--spacing-4) $prime_purple;
        border-radius: 0 var(--spacing-80) var(--spacing-80) 0;
        @media (max-width:768px) {
            padding-left: var(--spacing-24);
        }
    }
}